/** Media Queries **/
#banner {
  font-size: 3.1rem;
  text-align: center;
  padding: 4rem 4rem 1rem 4rem;
  width: 50rem;
  position: absolute;
  z-index: 100;
  top: 20rem;
  right: 10rem;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.5);
}

.green-button {
  text-transform: uppercase;
  border: 0;
  display: inline-flex;
  align-items: center;
  font-size: 1.4rem;
  padding: 1rem 4rem;
  background-color: #71b100;
  color: #000000;
  font-weight: bolder;
  text-decoration: none;
}
.green-button:hover {
  background-color: #5a8e00;
}

.green-link {
  text-transform: uppercase;
  border: 0;
  display: inline-flex;
  position: relative;
  align-items: center;
  font-size: 1.6rem;
  padding: 1rem 4rem;
  color: #71b100;
  font-weight: bolder;
  text-decoration: none;
}
.green-link::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.2rem;
  transform: scaleX(0);
  transition: transform 0.3s ease;
  background-color: #71b100;
}

.green-link:hover::before {
  transform: scaleX(0.6);
}

@media (max-width: 1458px) {
  #banner {
    position: initial !important;
    z-index: 100;
    width: 100%;
  }
}
@media (max-width: 437px) {
  #banner .h2-sub::before {
    top: 16rem;
    left: 0.5rem;
  }
}